import helper from '../../common/helper.js';
var dateUtils = require('../../common/util.js').dateUtils;
export default {
	data() {
		return {
			infoArray: '',
			listData: [],
			datatime: '',
		};
	},
	onLoad() {
		this.getList()
	},
	methods: {
		openmascontent(e) { //打开系统消息内容页
			uni.navigateTo({
				url: 'messages-content?xxid=' + e.currentTarget.dataset.xxid 
			});
		},
		getList() {
			var data = {
				column: "Content,CreateDate,ID,Title,"     //需要的字段名
			};
			uni.request({
				url: helper.websiteUrl + 'message/getmessagelist',
				method: 'GET',
				data: helper.postdata({
					"num": 10,
					"page": 1,
				}),
				success: (res) => {
					if (res.data.code == 200) {
						let list = this.setTime(res.data.data.baseinfo);
						this.listData = this.reload ? list : this.listData.concat(list);
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			})
		},

		setTime: function(items) {
			var newItems = [];
			items.forEach((e) => {
				newItems.push({
					Content: e.Content,
					ID: e.ID,
					CreateDate: dateUtils.format(e.CreateDate),
					Title: e.Title
				});
			});
			return newItems;
		}
	},

}
